
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
    background-color: #23252c;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family:Inter;
}

/* Info Section */

.info, .footer, main{
    margin:0 auto;
    width:317px;
    margin-top:44px;
    color:white;
    background-color:#23252C;
}

.info{
    text-align: center;
    margin:auto;
    
}
.profile-img{ 
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    height:317px;
    width: 317px;
    margin-top:41px; 

}
.profile-name{
    padding-top:20px;
    font:25px, bold;
 
}
.profile-title{
    padding-top:10px;
    margin-top:6px;
    font-size :15px;
    color:#F3BF99;
}   
.profile-website{
    margin-top:8px;
    font-size:12px;
}

button{
    margin-top:15px;
    padding:9px, 13px, 9px, 11px;
    width:115px;
    height:38px;
    border-radius:5px;
    font:14px medium;
}
a{
    text-decoration:none;
    color:black;
}
.email{
    border:1px solid #D1D5DB;
    color:#374151;
    background-color:white;
}
.contact{
    border:1px solid #5093E2;
    margin-left:17px;
    color:white;
    background-color:#5093E2;
}

.phone-icon, .email-icon{
    padding-right:8px;
}

/* Main */
.footer, main{
    background-color:#1A1B21;
}

.shade, main{
    background-color: #1A1B21;
}
main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    padding: 0 25px 48px 25px;
    margin-top:0px;

  }

.about-title,.interest-title{
    padding-top:27px;
    margin-right:35px;
    width: 317px;
    text-align: left;
    font-weight: bold;
    font-size: 20px;
    color:#F5F5F5;
}
.about-me,.interest-me{
    padding-top:5px;
    font-size:15px;
    color:#DCDCDC;
}

/* Footer*/

.footer{ 
    margin-top:-20px;
    background-color:#161619;
    margin-bottom:44px;
    padding-bottom:20px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    border-bottom-left-radius:10px;
    border-bottom-right-radius: 10px;
}
.icons{
    height: 25px;

}
.twitter-icon,.github-icon,.linkedin-icon{
    padding:10px;
    height:25px;
    
    
}
